import React from "react";
import {Button, Card, Form, Input, Space, Upload} from "antd";
import "antd/dist/antd.css";
import axios from "axios";
import {UploadOutlined} from "@ant-design/icons";


function App() {

    const onFinish = async (formValue) => {
        let data = new FormData();
        data.append('file', formValue.file.file.originFileObj)
        data.append('fileName', formValue.fileName)
        data.append('fileFormat', formValue.fileFormat)
        data.append('issueTracking', formValue.issueTracking)
        data.append('extorgCode', formValue.extorgCode)
        data.append('extorgName', formValue.extorgName)
        data.append('protocolName', formValue.protocolName)
        data.append('userName', formValue.userName)

        console.warn(formValue);
        console.warn(formValue.file.file);
        console.log(data);
        try {
            let url = "http://localhost:8080/upload";
            axios.post(url, data, {}).then(res => {
                console.warn(res);
            })
        } catch (error) {
            console.log(error)
        }
    }

    const props = {
        action: '//jsonplaceholder.typicode.com/posts/',
        // listType: 'picture',
    }

    const layout = {
        labelCol: {
            span: 9,
        },
        wrapperCol: {
            span: 16,
        },
    };

    return (
        <div className="App">
            <Card
                bodyStyle={{
                    width: 400,
                    margin: "0 auto"
                }}
            >
                <Form {...layout}
                      onFinish={onFinish}>
                    <Form.Item
                        name="file"
                        label="url link: "
                        rules={[
                            {
                                required: true,
                            },
                        ]}>
                        <Upload  {...props}>
                            <Button icon={<UploadOutlined />}>Upload</Button>
                        </Upload>
                    </Form.Item>
                    <Form.Item
                        name="fileName"
                        label="filename link: "
                        rules={[
                            {
                                required: true,
                            },
                        ]}>
                        <Input placeholder="Sana_DMK_test2"/>
                    </Form.Item>
                    <Form.Item
                        name="fileFormat"
                        label="fileFormat link: "
                        rules={[
                            {
                                required: true,
                            },
                        ]}>
                        <Input placeholder="XLSX,XLS,XML"/>
                    </Form.Item>
                    <Form.Item
                        name="issueTracking"
                        label="issueTraking link: "
                        rules={[
                            {
                                required: true,
                            },
                        ]}>
                        <Input placeholder="null"/>
                    </Form.Item>
                    <Form.Item
                        name="extorgCode"
                        label="orgCode link: "
                        rules={[
                            {
                                required: true,
                            },
                        ]}>
                        <Input placeholder="221520"/>
                    </Form.Item>
                    <Form.Item
                        name="extorgName"
                        label="orgName link: "
                        rules={[
                            {
                                required: true,
                            },
                        ]}>
                        <Input placeholder="     "/>
                    </Form.Item>
                    <Form.Item
                        name="protocolName"
                        label="protocolName link: "
                        rules={[
                            {
                                required: true,
                            },
                        ]}>
                        <Input placeholder="DMK"/>
                    </Form.Item>
                    <Form.Item
                        name="userName"
                        label="userName link: "
                        rules={[
                            {
                                required: true,
                            },
                        ]}>
                        <Input placeholder="service_dmkt2"/>
                    </Form.Item>
                    <Form.Item wrapperCol={{...layout.wrapperCol, offset: 8}}>
                        <Space>
                            <Button type="primary" htmlType="submit">
                                OK
                            </Button>
                        </Space>
                    </Form.Item>
                </Form>
            </Card>
        </div>
    );
}

export default App;